import { Component, Inject, OnInit } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { FormlyFieldConfig } from '@ngx-formly/core';
import { Menu, getMenuFormlyFields } from 'src/app/interfaces/Menu';
import { CommonModule } from '@angular/common';
import { ReactiveFormsModule } from '@angular/forms';
import { FormlyModule } from '@ngx-formly/core';
import { FormlyMaterialModule } from '@ngx-formly/material';
import { MatDialogModule } from '@angular/material/dialog';
import { MatButtonModule } from '@angular/material/button';
import { TranslateModule, TranslateService } from '@ngx-translate/core';
import { Roles } from 'src/app/interfaces/Roles';
import { Category } from 'src/app/interfaces/Category';

@Component({
  selector: 'app-create-update-menu',
  standalone: true,
  imports: [
    CommonModule,
    ReactiveFormsModule,
    FormlyModule,
    FormlyMaterialModule,
    MatDialogModule,
    MatButtonModule,
    TranslateModule
  ],
  templateUrl: './create-update-menu.component.html',
  styleUrls: ['./create-update-menu.component.scss']
})
export class CreateUpdateMenuComponent implements OnInit {
  form = new FormGroup({});
  model: Partial<Menu> = {
    name: '',
    icon: '',
    color: '',
    route: '',
    roles: [],
    category_id: 1,
    disable: 0
  };
  fields: FormlyFieldConfig[] = [];
  isEditMode = false;
  mode: 'create' | 'update' = 'create';

  constructor(
    public dialogRef: MatDialogRef<CreateUpdateMenuComponent>,
    @Inject(MAT_DIALOG_DATA)
    public data: {
      menu?: Menu;
      mode: 'create' | 'update';
      roles: Roles[];
      categories: Category[];
    },
    private translate: TranslateService
  ) {}

  ngOnInit(): void {
    this.mode = this.data.mode;
    this.initForm();

    if (this.data.menu) {
      this.isEditMode = true;
      this.updateMode();
    }
  }
  
  private initForm(): void {
    this.fields = getMenuFormlyFields(
      this.translate,
      this.data.roles,
      this.data.categories,
      () => this.onSubmit(),
      () => this.onCancel(),
      () => this.form.invalid
    );
  }

  private updateMode(): void {
    if (this.data.menu) {
      const menu = this.data.menu;
      let selectedRoles: string[] = [];
      if (menu.roles && Array.isArray(menu.roles)) {
        selectedRoles = menu.roles.map((role) =>
          typeof role === 'object' && 'name' in role
            ? (role as { name: string }).name
            : (role as string)
        );
      }
      this.model = {
        id: menu.id,
        slug: menu.slug,
        name: menu.name,
        icon: menu.icon,
        color: menu.color,
        route: menu.route,
        roles: selectedRoles,
        category_id: menu.category_id,
        disable: menu.disable || 0,
        queryParams: menu.queryParams
      };
    }
  }

  onSubmit(): void {
    if (this.form.invalid) {
      return;
    }
    this.dialogRef.close(this.model);
  }

  onCancel(): void {
    this.dialogRef.close();
  }
}
