import { FormlyFieldConfig } from '@ngx-formly/core';
import { TranslateService } from '@ngx-translate/core';
import { Roles } from './Roles';
import { Category } from './Category';

export interface Menu {
  id?: number;
  slug: string;
  name: string;
  icon: string;
  color: string;
  route: string;
  roles: string[];
  queryParams?: { [key: string]: any };
  category_id: Category['id'];
  category?: Category;
  disable?: number;
}

export const MenuFormlyFields: FormlyFieldConfig[] = [
  {
    key: 'name',
    type: 'input',
    props: {
      label: 'Menu Name',
      placeholder: 'Enter the menu name',
      required: true,
      minLength: 2,
      maxLength: 50
    },
    validation: {
      messages: {
        required: 'Menu name is required',
        minlength: 'Menu name must be at least 2 characters',
        maxlength: 'Menu name cannot exceed 50 characters'
      }
    }
  },
  {
    key: 'icon',
    type: 'input',
    props: {
      label: 'Icon',
      placeholder: 'Enter icon name (e.g., mat:home)',
      required: true,
      minLength: 4,
      maxLength: 50
    },
    validation: {
      messages: {
        required: 'Icon is required',
        minlength: 'Icon name must be at least 4 characters',
        maxlength: 'Icon name cannot exceed 50 characters'
      }
    }
  },
  {
    key: 'color',
    type: 'input',
    props: {
      label: 'Color CSS Class',
      placeholder: 'Enter CSS color class (e.g., bg-blue-600)',
      required: true,
      minLength: 5,
      maxLength: 30
    },
    validation: {
      messages: {
        required: 'Color class is required',
        minlength: 'Color class must be at least 5 characters',
        maxlength: 'Color class cannot exceed 30 characters'
      }
    }
  },
  {
    key: 'route',
    type: 'input',
    props: {
      label: 'Route',
      placeholder: 'Enter the route path (e.g., /index/dashboard)',
      required: true,
      minLength: 2,
      maxLength: 100
    },
    validation: {
      messages: {
        required: 'Route is required',
        minlength: 'Route must be at least 2 characters',
        maxlength: 'Route cannot exceed 100 characters'
      }
    }
  },
  {
    key: 'category_id',
    type: 'select',
    props: {
      label: 'Category',
      placeholder: 'Select menu category',
      required: true
    },
    validation: {
      messages: {
        required: 'Category is required'
      }
    }
  },
  {
    key: 'roles',
    type: 'select',
    props: {
      label: 'Roles',
      placeholder: 'Select roles that can access this menu',
      required: true,
      multiple: true
    },
    validation: {
      messages: {
        required: 'At least one role must be selected'
      }
    }
  },
  {
    key: 'disable',
    type: 'slide-toggle',
    props: {
      label: 'disable'
    }
  }
];

export function getMenuFormlyFields(
  translate: TranslateService,
  rolesList: Roles[],
  categoriesList: Category[],
  Save: () => void,
  Cancel: () => void,
  isDisabled: () => boolean
): FormlyFieldConfig[] {
  const roleOptions = rolesList.map((role) => ({
    value: role.name, // La valeur stockée sera le nom du rôle
    label: role.name // Le libellé affiché
  }));

  const categoryOptions = categoriesList.map((category) => ({
    value: category.id,
    label: category.name
  }));

  return [
    {
      key: 'name',
      type: 'input',
      props: {
        label: translate.instant('menu.label.name'),
        placeholder: translate.instant('menu.label.name'),
        required: true,
        minLength: 2,
        maxLength: 50
      },
      validation: {
        messages: {
          required: 'Menu name is required',
          minlength: 'Menu name must be at least 2 characters',
          maxlength: 'Menu name cannot exceed 50 characters'
        }
      }
    },
    {
      key: 'icon',
      type: 'material-icon-select',
      props: {
        label: translate.instant('menu.label.icon'),
        required: true
      },
      validation: {
        messages: {
          required: 'Icon is required'
        }
      }
    },
    {
      key: 'color',
      type: 'tailwind-color-select',
      props: {
        label: translate.instant('menu.label.color'),
        required: true
      },
      validation: {
        messages: {
          required: 'Color class is required'
        }
      }
    },
    {
      key: 'route',
      type: 'input',
      props: {
        label: translate.instant('menu.label.route'),
        placeholder: translate.instant('menu.label.route'),
        required: true,
        minLength: 2,
        maxLength: 100
      },
      validation: {
        messages: {
          required: 'Route is required',
          minlength: 'Route must be at least 2 characters',
          maxlength: 'Route cannot exceed 100 characters'
        }
      }
    },
    {
      key: 'category_id',
      type: 'select',
      props: {
        label: translate.instant('menu.label.category'),
        required: true,
        options: categoryOptions
      },
      validation: {
        messages: {
          required: 'Category is required'
        }
      }
    },
    {
      key: 'roles',
      type: 'select',
      props: {
        label: translate.instant('menu.label.roles'),
        required: true,
        multiple: true,
        options: roleOptions
      },
      validation: {
        messages: {
          required: 'At least one role must be selected'
        }
      }
    },
    {
      key: 'disable',
      type: 'slide-toggle',
      props: {
        label: translate.instant('menu.label.disable')
      }
    },
    {
      fieldGroupClassName: 'flex justify-end gap-2 mt-4',
      fieldGroup: [
        {
          type: 'button',
          props: {
            text: translate.get('global.cancel'),
            color: 'default',
            type: 'button',
            className: 'bg-white text-black mat-flat-button',
            onClick: () => Cancel()
          }
        },
        {
          type: 'button',
          props: {
            text: translate.get('global.save'),
            color: 'primary',
            type: 'button',
            className: 'mat-flat-button',
            onClick: () => Save()
          },
          expressionProperties: {
            'props.disabled': () => isDisabled()
          }
        }
      ]
    }
  ];
}

export function getManageMenuFiltersFormlyFields(
  categoriesList: Category[],
  rolesList: Roles[],
  categoryAllLabel: string,
  roleAllLabel: string,
  categoryLabel: string,
  roleLabel: string,
  onCategoryChange: (categoryId: number | null) => void,
  onRoleChange: (roleName: string | null) => void
): FormlyFieldConfig[] {
  const categoryOptions = [
    { value: null, label: categoryAllLabel },
    ...categoriesList.map((category) => ({
      value: category.id,
      label: category.name
    }))
  ];

  const roleOptions = [
    { value: null, label: roleAllLabel },
    ...rolesList.map((role) => ({
      value: role.name,
      label: role.name
    }))
  ];

  return [
    {
      fieldGroupClassName: 'flex items-center gap-4 mb-2',
      fieldGroup: [
        {
          key: 'category_filter',
          type: 'select',
          className: 'w-64',
          props: {
            label: categoryLabel,
            appearance: 'outline',
            options: categoryOptions,
            change: (field: any, $event: any) => {
              onCategoryChange($event.value);
            }
          }
        },
        {
          key: 'role_filter',
          type: 'select',
          className: 'w-64',
          props: {
            label: roleLabel,
            appearance: 'outline',
            options: roleOptions,
            change: (field: any, $event: any) => {
              onRoleChange($event.value);
            }
          }
        }
      ]
    }
  ];
}
